}

/* ==========================================================================
    HORIZONTAL RULE
   ========================================================================== */

hr {
  border-style: none;
  height: 1px;
  padding: 0;
  --bg-opacity: 1;
  background-color: #e2e8f0;
  background-color: rgba(226, 232, 240, var(--bg-opacity));
  margin-bottom: 2rem;
}

/* ==========================================================================
    STRONG AND BOLD
   ========================================================================== */

b,
strong {
}

/* ==========================================================================
    SUPER AND SUBSCRIPTS
   ========================================================================== */

sup {
  font-feature-settings: "sups";
  font-variant-position: super;
}

sub,
.sinf {
  font-size: inherit;
  font-feature-settings: "subs";
  font-variant-position: sub;
}

/* ==========================================================================
    TABLES
   ========================================================================== */

table {
  border-collapse: collapse;
  /* Turn on lining and proportional numerals
  font-variant-numeric: lining-nums;
  font-variant-numeric: proportional-nums;
  */
}

tbody,
caption {
  /* Turn on lining, tabular numerals, slashed zero
  font-variant-numeric: lining-nums;
  font-variant-numeric: slashed-zero;
  font-variant-numeric: tabular-nums;
   */
}

th {
  text-align: inherit;
}

td,
th {
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

tr:nth-of-type(odd) {
  }

/* Utilities */

[class^='aspect-ratio-'],
[class*=' aspect-ratio-'] {
  display: block;
  position: relative
}

[class^='aspect-ratio-']:before, [class*=' aspect-ratio-']:before {
    --bg-opacity: 1;
    background-color: #e2e8f0;
    background-color: rgba(226, 232, 240, var(--bg-opacity));
    display: block;
    width: 100%;
    content: '';
  }

[class^='aspect-ratio-'] .aspect-inner, [class*=' aspect-ratio-'] .aspect-inner {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }

.aspect-ratio-category-card:before {
  padding-top: calc((2.5 / 4) * 100%);
}

.aspect-ratio-hero:before {
  padding-top: calc((2 / 5) * 100%)
}

@media (min-width: 1440px) {

.aspect-ratio-hero:before {
    padding-top: 0;

    height: 36rem
}
  }

.aspect-ratio-hero-short:before {
  padding-top: calc((1 / 5) * 100%)
}

@media (min-width: 1440px) {

.aspect-ratio-hero-short:before {
    padding-top: 0;

    height: 18rem
}
  }

.aspect-ratio-product-card:before {
  padding-top: calc((2 / 3) * 100%);
}

.aspect-ratio-article-card:before {
  padding-top: calc((1.15 / 2) * 100%);
}

.center {
  top: 50%;
  left: 50%;
  transform: translate( -50%, -50% );
}

.center-y {
  top: 50%;
  transform: translateY( -50% );
}

.center-x {
  left: 50%;
  transform: translateX( -50% );
}

.hide {
  top: -99999px;
  left: -99999px;
  pointer-events: none;
  opacity: 0;
  transition: all .3s ease-in-out;
}

.visually-hidden-checkbox {
  pointer-events: none;
  position: absolute;
  opacity: 0;
  height: 2rem;
  width: 2rem;
}

.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap; /* 1 */
}

/* Components */

.button {
  -webkit-appearance: none;
          appearance: none;
  --bg-opacity: 1;
  background-color: #e2e8f0;
  background-color: rgba(226, 232, 240, var(--bg-opacity));
  cursor: pointer;
  display: inline-block;
  text-decoration: none;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  text-align: center;
}

.button:hover {
  --bg-opacity: 1;
  background-color: #a0aec0;
  background-color: rgba(160, 174, 192, var(--bg-opacity));
}

